//FROM 993PX --------------------------------------------------------------------------------------
@media (min-width: $break-med) {
        /////////////////////////////////////////////////////////////////////////////////////////
        //HEADER/ NAV1 //////////////////////////////////////////////////////////////////////////
        nav {
            width: 970px;
        }

        //increase logo size
        #logo img {
            width: 370px;
        }

        //hide search input
        #search {
            display: none;
        }

        //change search bar container width to the size of the button
        #search-bar {
            width: 63px;
            margin-left: 20px;
        }

        //search button rounded corners
        #search-bar button[type=button] {
            border-radius: $round-corner;
        }

        //NAV support and contact buttons appear
        #support,
        #contact {
            display: inline-block;
        }

        #support {
            margin-left: auto; 
        }


        //////////////////////////////////////////////////////////////////////////////////////////
        //NAV BAR/ NAV2///////////////////////////////////////////////////////////////////////////
        //Nav bar appears
        .nav-2 {
            display: flex;
        }
     

        /////////////////////////////////////////////////////////////////////////////////////////
        //SLIDER/ BANNER ////////////////////////////////////////////////////////////////
        //Banner text aligns left & image gradient changes
        .banner {
            align-items: flex-start;
            text-align: left;
            background:  linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0.4042659300048145) 35%, rgba(0,0,0,0) 100%),
                        url("../../html-css-reflection/images/slides/slide-1.jpeg");
            background-size: cover;
            height: 490px;
        }

        .banner-container > h1,
        .banner-container > span {
            margin-left: 0;
        } 


        ///////////////////////////////////////////////////////////////////////////////////////////
        //BANNER /INTRO /LATEST TITLE WIDTH/////////////////////////////////////////////////////
        .banner-container,
        .intro-container,
        #latest-title {
            width: 940px;
        }

        ///////////////////////////////////////////////////////////////////////////////////////////
        //CARDS ///////////////////////////////////////////////////////////////////////////////// 
        .cards {
            width: 940px; ///////////maintain equal margins
        }

        //CARD format 3 + 2 + 2
        .card-row-1 {
            width: 293.333px;
            height: 356px;
            & > p {
                height: 110px; 
            }
        }

        .card-software {
            margin-left: 0;
        }

        .card-marketing {
            margin-right: 0;
        }

        .card-row-2 {
            width: 455px;
            & > p {
                height: 44px;
            }
        }


        /////////////////////////////////////////////////////////////////////////////////////
        //INTRO ////////////////////////////////////////////////////////////////////////////
        //Intro background image added
        .intro {
            background: $main-grey url("../../html-css-reflection/images/internal-branding/logo-zoomed-transparent.png") no-repeat right bottom;
            background-size: cover;
        }


        ///////////////////////////////////////////////////////////////////////////////////////////
        //LATEST ////////////////////////////////////////////////////////////////////////////////
        //News items new width
        .news-container {
            width: 970px;
        }
        
        .news {
            width: 455px;
        }

        .news-image-parent {                  /////////////////////////////////for animation
            display: inline-block;
            width: 455px;
            height: 261.844px;
            overflow: hidden;
            &:hover > .news-image-child {
                transform: scale(1.075);
            }
        }

        .news-image-child {
            width: 100%;
            height: 100%;
            border-radius: 3px 3px 0px 0px;
            transition: all .25s cubic-bezier(.215,.61,.355,1);
        }

        .news-1-image-child {           
            background: url("../../html-css-reflection/images/latest/news-1.jpeg") no-repeat center;
            background-size: cover;
        }

        .news-2-image-child {           
            background: url("../../html-css-reflection/images/latest/news-2.jpeg") no-repeat center;
            background-size: cover;
        }
        
        .news-3-image-child {           
            background: url("../../html-css-reflection/images/latest/news-3.jpeg") no-repeat center;
            background-size: cover;
        }

        .news > a > img {
            display: none;
        }

        .news-text {
            border-top: none;
            margin-top: -3px;
        }

        //NEWS TOOLTIPS 
        .news .tooltip .tooltip-text {
            width: 200px;
            margin-right: -100px;
        }


        ////////////////////////////////////////////////////////////////////////////////////////
        //FOOTERS ///////////////////////////////////////////////////////////////////////
        //Footer width
        .footer-logos,
        .partner-footer {
            width: 940px;
            margin: 0 auto;
        }

        .main-foot-cont {
            width: 970px;
            margin: 0 auto;
        }

        //FOOTER HEIGHT
        .partner-footer {
            height: 210px;
        }

        //Main footer sub-section widths        
        .main-foot-section {
            width: 485px;
        }

        //FOOTER CLIENT AND PARTNER LOGO RESIZING. Not sure about the larger partner logos at this size - visually prefer the settings for the next screen up due to image quality and spacing.     
        #busseys {
            width: 168.7px;
            height: 62.8px;
        } 

        #crane {
            width: 260px; 
            height: 62.8px;
        }

        #beat { 
            width: 64.6px; 
            height: 62.8px;
        }

        #northern-diver {
            width: 289px; 
            height: 62.8px;
        }

        #microsoft {
            width: 140.2px;
            height: 85.3px;
        } 

        #future-50 {
            width: 69.2px; 
            height: 110.1px;
        }

        #qms { 
            width: 85.2px; 
            height: 99.4px;
        }

        #carbon-charter {
            width: 200.2px; 
            height: 80.1px;
        }

        //SOCIAL BUTTON WIDTH
        .social-button li a {
            width: 212.5px;
        }
    }



//FROM 1260PX -----------------------------------------------------------------------------
@media (min-width: $break-lg) {  

    //////////////////////////////////////////////////////////////////////////////////////////////
    //HEADER / NAV-1 ////////////////////////////////////////////////////////////////////////////
    nav {
        width: 1200px;
    }

    //bring back search input
    #search {
        display: inline-block;
    }

    #search-bar input[type=text] {
        width: 240px;
    }

    #search-bar {
        width: 303px;
    }

    //re-square the search button inner corners
    #search-bar button[type=button] {
        border-radius: 0px 3px 3px 0px;
    }

    ///////////////////////////////////////////////////////////////////////////////////////////
    //NAV-BAR / NAV-2 ////////////////////////////////////////////////////////////////////////

    //Resize nav menu boxes
    .nav-box-text { 
        width: 192px;
    }

    //Nav dropdowns font-size
    .nav-dropdown {
        font-size: 16px;
    }

    .secondary-nav-text {
        width: 1152px;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////
    //BANNER/ INTRO TEXT/ LATEST TITLE WIDTH /////////////////////////////////////////////////////
    .banner-container,
    .intro-container,
    #latest-title {
        width: 1170px;
    }

    
    ///////////////////////////////////////////////////////////////////////////////////////////
    //CARDS //////////////////////////////////////////////////////////////////////////////////
    //Cards format
    .cards {
        width: 1170px; ///////////maintain equal margins
    }

    // Cards format 3 + 4.
    .card-row-1 {
        width: 370px;
        height: 312px;
        & > p {
            height: 66px;       
        }
    }

    .card-row-2 {
        width: 270px;
        & >  p {
            height: 66px;
        }
    }

    .card-web {
        margin-right: auto;
    }

    .card-cyber {
        margin-left: auto;
    }


    ////////////////////////////////////////////////////////////////////////////////////////
    //INTRO ///////////////////////////////////////////////////////////////////////////////
    .intro-container > h1,
    .intro-container > p {
        width: 570px;
    }

    /////////////////////////////////////////////////////////////////////////////////////////
    //LATEST ////////////////////////////////////////////////////////////////////////////
    .news-container {
        width: 1170px;
    }

    //Latest news items resized for 3 columns
    .news {
        width: 370px;
    }

    .news-1 {
        margin-right: 15px;
        margin-left: 0;
    }

    .news-2 {
        margin-left: auto;//15px;
        margin-right: auto; //15px;
    }

    .news-3 {
        margin-left: 15px;
        margin-right: 0;
    }

    //Latest 3rd news item added again.
    .news-3 {
        display: inline-block; 
    }

    //Latest news image animation
    .news-image-parent { 
        width: 370px; 
        height: 212.708px;
        overflow: hidden;
    }

    
    //////////////////////////////////////////////////////////////////////////////////
    //FOOTER /////////////////////////////////////////////////////////////////////////////
    //Footer heights
    .main-footer {
        height: 309px;
    }

    .partner-footer {
        height: 188px;
    }

    //Footer widths
    .footer-logos,
    .partner-footer {
        width: 1170px;
        margin: 0 auto;
    }

    .main-foot-cont {
        width: 1200px;
        margin: 0 auto;
    }

    //Footer client logos - SWITCH TO FULL SIZE IMAGE SPRITES
    #busseys {
        width: 188px; 
        height: 70px;
        background: url("../../html-css-reflection/images/external-branding/clients/busseys-sprite.png") -10px -10px;
        &:hover {
            background: url("../../html-css-reflection/images/external-branding/clients/busseys-sprite.png") -10px -100px;
        }
    }
    
    #crane {
        width: 287px; 
        height: 70px;
        background: url("../../html-css-reflection/images/external-branding/clients/crane-sprite.png") -10px -10px;
        &:hover {
            background: url("../../html-css-reflection/images/external-branding/clients/crane-sprite.png") -10px -100px;
        }
    }
    
    #beat { 
        width: 72px; 
        height: 70px;
        background: url("../../html-css-reflection/images/external-branding/clients/beat-sprite.png") -10px -10px;    
        &:hover {
            background: url("../../html-css-reflection/images/external-branding/clients/beat-sprite.png") -102px -10px;
        }
    }  
    
    #northern-diver {
            width: 322px; 
            height: 70px;
            background: url("../../html-css-reflection/images/external-branding/clients/northern-diver-sprite.png") -10px -10px;
            &:hover {
                background: url("../../html-css-reflection/images/external-branding/clients/northern-diver-sprite.png") -10px -100px;
        }
    }
    
    //Footer client logos - SWITCH TO FULL SIZE IMAGE SPRITES
    #microsoft {
        width: 115px;
        height: 70px;
        background: url("../../html-css-reflection/images/external-branding/other/microsoft-bw.jpeg");
        background-size: cover;
    }

    #future-50 {
        width: 44px; 
        height: 70px;
        background: url("../../html-css-reflection/images/external-branding/other/future-50-sprite.png") -10px -10px;
        &:hover {
            background: url("../../html-css-reflection/images/external-branding/other/future-50-sprite.png") -74px -10px;
        }
    }

    #qms {
        width: 60px; 
        height: 70px;
        background: url("../../html-css-reflection/images/external-branding/other/qms-sprite.png") -10px -10px;
        &:hover {
            background: url("../../html-css-reflection/images/external-branding/other/qms-sprite.png") -90px -10px;
        }
    }

    #carbon-charter {
        width: 175px; 
        height: 70px;
        background: url("../../html-css-reflection/images/external-branding/other/carbon-charter-sprite.png") -10px -10px;
        &:hover {
            background: url("../../html-css-reflection/images/external-branding/other/carbon-charter-sprite.png") -10px -100px;
        }
    }

    //Main footer switches to 4 column layout 
    .main-foot-section {
        width: 300px;
    }

     //Social button width
     .social-button li a {
        width: 120px;
    }
}
  

