//MAIN-FOOTER //////////////////////////////////////////////////////////////////////////////////////////////////////////////////
.main-footer {
    display: flex;
    flex-direction: column;
    width: 100%;
    font-size: 16px;
    line-height: 22.8571px;
    color: $white;
    background-color: $main-grey;
    & a {
        font-size: 13px;
        line-height: 22px;
        color: $white;
        &:hover {
            text-decoration: underline;
        }
    }
}

.main-foot-cont {
    height: 100%;
    width: 100%;
    padding-top: 47px;
    padding-bottom: 43px;   
    display: flex;
    flex-wrap: wrap;
}

//Sub-sections
.main-foot-section {
    padding: 0px 15px;
    width: 100%;
    & > h2 {
        text-transform: uppercase;
        font-weight: $regular;
        margin-top: 0;
        font-size: 16px;
        line-height: 17.6px;
        width: 95%;
    }
}

.main-foot-section:not(:last-child) {
    margin-bottom: 30px;
    & > h2 {
        margin-bottom: 26px;
    }
}

.social {
    height: 190px;
    & > h2 {
        margin-bottom: 0; //adjusted for social button top margin of 30px
    }
}
  
.main-foot-section > ul {
    list-style-type: none;
    padding-left: 0;
    margin-bottom: 0;
    & > li {
        font-size: 13px;
        line-height: 22px;
    }
}


////////////////////////////////////////////////////////////////////////////////////////////////////////////////
//CLIENT FOOTER //larger screens only ////////////////////////////////////////////////////////////////////////////

.client-footer {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: 100%;
    width: 100%;
    background-color: $white;
    padding-top: 50px;
    padding-bottom: 50px;
    box-shadow: inset 0px 5px 35px 0px rgba(0, 0, 0, 0.1);
}

.footer-logos {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

