//TOOLTIPS /////////////////////////////////////
.client-footer .tooltip {
    position: relative;
    &:hover .tooltip-text {
        display: inline-block;
    }
}

.client-footer .tooltip-text {
    display: none;
    background-color: $main-grey;
    color: $white;
    text-align: center;
    padding: 20px;
    font-size: 13px;
    line-height: 22px; 
    margin-bottom: 50px; //positions the tooltip on the footer line
    //Position the tooltip text
    position: absolute;
    z-index: 5;
    width: 260px;
    bottom: 100%;
    left: 50%;
    margin-left: -130px; //Use half of the width (120/2 = 60), to center the tooltip
}

//bottom arrow
.client-footer .tooltip-text::after {
    content: " ";
    position: absolute;
    top: 100%; /* At the bottom of the tooltip */
    left: 50%;
    margin-left: -36px;
    border-width: 36px;
    border-style: solid;
    border-color: $main-grey transparent transparent transparent;
  }

.footer-logos .tooltip-text p {
    margin-bottom: 0;
}

.footer-logos .tooltip-title {
    font-size: 18px;
    padding-bottom: 17px;
    margin-top: 0;
    margin-bottom: 11px;
    font-weight: $regular;
    position: relative;
}

.footer-logos .tooltip-title::after {  //////////EXACT SAME SETTINGS AS THE CARDS - CREATE MIXIN?
    content: "";
    position: absolute;
    width: 30px;
    top: 35px;
    left: calc(50% - 15px);
    border-bottom: 3px solid $white;
}

